export default {
	data() {
		return {
			show_popups:false,
			active:1
		}
	},
	methods: {
		random:function(){
			uni.navigateTo({
				url:"randomName"
			})
		},
		letter:function(){
			uni.navigateTo({
				url:"lettersName"
			})
			
		},
		tab:function(e){
			this.active=e
		},
		girlsEnglishName:function(){
			uni.navigateTo({
				url:'girlsEnglishName'
			})
		},
		boysEnglishName:function(){
			uni.navigateTo({
				url:'boysEnglishName'
			})
		},
		neutralEnglishName:function(){
			uni.navigateTo({
				url:'neutralEnglishName'
			})
		},
		search:function(){
			this.show_popups=true
			uni.pageScrollTo({
			    scrollTop: 0,
			    duration:0
			});
		},
		cancel:function(){
			var that=this;
			that.guanle()
		},
		guanle:function(){
			var that=this;
			that.show_popups=false;
			console.log(that.show_popups)
		}
		
	}
}
